@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:700);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:300);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:100);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-class {
  position: fixed;

  width: 95vw;
}
.header-text {
  float: right;
  background: rgba(57, 127, 218, 0.6);
  color: white;
  height: 64px;
  width: 775px;
  border-radius: 30px 10px;
}
.header-class ul {
  list-style: none;
  margin: 0;
}
.header-li {
  font-family: "Roboto", sans-serif;
  margin-right: 3rem;
  padding: 1.5rem;
  float: right;
  border: 0;
  cursor: pointer;
  opacity: 0.6;
}

.header-class ul li:hover {
  opacity: 1;
}

.blank_white {
  background: transparent;
  width: 40%;
  height: 64px;
}

body {
  overflow-x: hidden;
}
html {
  scroll-behavior: smooth;
}
.bar_menu_container {
  display: grid;
  justify-content: flex-end;
}
.bar_open {
  padding: 20px;
  cursor: pointer;

  margin: 0;
}
.bar1,
.bar2,
.bar3 {
  width: 35px;
  height: 5px;
  background-color: rgba(57, 127, 218, 0.8);
  opacity: 0.8;
  margin: 6px 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.bar_close {
  padding: 20px;
  padding-left: 0;
  cursor: pointer;

  margin: 0;
}
.bar_close .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}
.bar_close .bar2 {
  opacity: 0;
}
.bar_close .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}

.bar_menu {
  display: grid;
  color: aliceblue;
  font-size: 17px;
  list-style: none;
  padding: 0px;
  grid-gap: 21px;
}
.bar_li {
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  color: aliceblue;
  opacity: 0.7;
}

@-webkit-keyframes blink {
  from,
  to {
    border-color: transparent;
  }

  50% {
    border-color: white;
  }
}

@keyframes blink {
  from,
  to {
    border-color: transparent;
  }

  50% {
    border-color: white;
  }
}

.typer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image: url(/React-PortFolio/static/media/bg2.e93498ae.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  color: lightskyblue;
  width: 100%;
}

.typer-text {
  font-family: "Noto Sans KR", sans-serif;
  border-right: 0.1em solid white;
  font-size: 5vw;
  white-space: pre;
  line-height: 1;
  letter-spacing: 0.2rem;
  padding-top: 0.2em;
  -webkit-animation: blink 1s step-end infinite;
          animation: blink 1s step-end infinite;
}
.sub-text {
  font-family: "Noto Sans KR", sans-serif;
  color: white;
  font-size: 3vw;
  line-height: 1;
  letter-spacing: 0.2rem;
}

.about_me {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0;
  background: ivory;
  height: 100%;
  width: 100%;
}

.about_me_title {
  margin-left: 6vw;
  margin-right: 12vw;
  width: 25vw;
  box-sizing: border-box;
  text-align: center;
  align-self: center;
}
.hedding {
  position: relative;
  display: inline-block;
  font-size: 2.4em;
  font-weight: 400;
  margin: 30px 0;
}
.hedding::after {
  position: absolute;
  content: "";
  top: 100%;
  height: 2px;
  width: 50px;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: rgb(123, 124, 212);
}
.about_profile {
  display: flex;
  justify-content: flex-start;
}
.about_me_content1 {
  position: relative;
  font-family: "Noto Sans KR", sans-serif;
  width: 45%;
  margin-left: 2vw;
}

.about_me_content2 {
  font-family: "Noto Sans KR", sans-serif;
  margin-top: 1rem;
  margin-bottom: 6rem;
  box-sizing: border-box;
  padding: 1rem;
  margin-right: 2vw;
  margin-left: 2vw;
  width: 55%;
}
.about_me_section {
  font-size: 17px;
}

/**/
.profile {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.personal_info {
  width: 100%;
  padding-left: 1rem;
  list-style: none;
}
.personal_info .name {
  font-size: 24px;
  font-weight: bold;
}
.personal_info > li {
  font-size: 18px;
}

.character {
  list-style: none;
  padding-left: 1rem;
  width: 100%;
  font-size: 17px;
}
.education {
  margin-top: 2rem;
}
.education h3 {
  margin-bottom: 0;
}
.edu_list {
  display: inline-flex;
  padding: 0;
  justify-content: flex-start;
  list-style: none;
  width: 100%;
  font-size: 18px;
}
.edu_list li {
  width: 50%;
}
.edu_list li p {
  margin: 0;
  font-weight: 400;
  margin-bottom: 0.2rem;
}

.edu_list li .date {
  margin-top: 1rem;
  font-size: 16px;
}

.career_list {
  list-style: none;
  padding: 0;
}

/**/
@media (max-width: 768px) {
  /*Tablet Size*/
  .about_me {
    display: inherit;
    height: 100%;
    width: 100%;
    text-align: center;
  }
  .about_me_title {
    display: inline-block;
    text-align: center;
    margin: 0 auto;
  }
  .about_profile {
    display: inherit;
    height: 100%;
    width: 100%;
    text-align: center;
  }
  .profile {
    display: inline-block;
    margin-top: 0px;
    width: 100%;
  }
  .personal_info {
    padding: 0;
  }
  .character {
    padding: 0;
  }
  .edu_list {
    display: inline-block;
    margin-top: 0px;
    width: 100%;
  }
  .edu_list li {
    width: 100%;
    padding-top: 1rem;
  }
  .career h3 {
    margin-bottom: 0;
  }

  .about_me_content1 {
    display: inline-block;
    margin-top: 0px;
    width: 100%;
  }
  .about_me_content2 {
    display: inline-block;
    margin-top: 0px;
    width: 100%;
    padding-top: 0;
  }
}

@media (max-width: 576px) {
  .typer-text {
    font-size: 7.5vw;
  }
  .sub-text {
    font-size: 6vw;
  }
}

.Skill {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding-bottom: 5rem;
  background: linen;
}
.Skill_Heeding {
  font-family: "Noto Sans KR", sans-serif;
  position: relative;
  display: inline-block;
  font-size: 2.4em;
  font-weight: 400;
  margin: 30px 0;
}
.Skill_Heeding::after {
  position: absolute;
  content: "";
  top: 100%;
  height: 2px;
  width: 30px;
  left: 0;
  right: 70%;
  margin: 0 auto;
  background: rgb(253, 198, 198);
}
.Skill_image_container {
  text-align: center;
  display: inline-block;
  width: 114px;
  height: 116px;
  border-radius: 30px 30px;

  background-size: cover;
  background-repeat: no-repeat;
  margin-right: 2rem;

  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2), 0 3px 6px rgba(0, 0, 0, 0.2);
}
.Skill_label {
  display: flex;
  justify-content: center;
  align-items: center;
  background: black;
  border-radius: 30px 30px;
  width: 100px;
  height: 100px;
  padding: 10px;
  opacity: 0.8;
  font-weight: 400;
  font-size: 1rem;
  color: white;
  visibility: hidden;
}

.Skill_image_container:hover .Skill_label {
  visibility: visible;
  animation: fadeout1 0.4s;
  -moz-animation: fadeout1 0.4s; /* Firefox */
  -webkit-animation: fadeout1 0.4s; /* Safari and Chrome */
  -o-animation: fadeout1 0.4s; /* Opera */
}
@keyframes fadeout1 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@-webkit-keyframes fadeout1 {
  /* Safari and Chrome */
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}

@media (max-width: 576px) {
  .Skill_image_container {
    width: 100px;
    height: 100px;
    margin: 5px;
  }
  .Skill_label {
    width: 84px;
    height: 84px;
  }
}

